@import '../../assets/scss/colors.scss'; 

.Orders {
    z-index: 5001;
    overflow: hidden;
    color: $main;
    width: 100%;
    position: relative;

    .Cancel {
        position: absolute;
        right: 0;
        top: 0;
        width: 5rem;
        height: 5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        font-size: 2rem;
        cursor: pointer;
    }

    .Random {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 100vh;

        div {
            h1 {
                padding: 1rem;
                margin: 0;
            }
        }

        .CTA {
            cursor: pointer;
            margin: 0;
            padding: 1rem;
            color: $main;
        }

        form {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            opacity: .9;

            input{
                width: 100%;
                max-width: 40rem;
                color: $main;
                border: none;
                background-color: transparent;
                border-bottom: 1px solid $main;
                margin-bottom: 1rem;
                padding: 1rem;
                text-align: center;
            }
            input::placeholder {
                color: darken($main, 10%);
            }
            select {
                background-color: $second;
                border: none;
                padding: 1rem;
                text-align-last: center;
                text-align: center;
                width: 100%;
                max-width: 40rem;
                color: darken($main, 10%);
            }
        }
    }
}